.App {
  text-align: center;
}

.App-header {
  background-color: #5bc7f5;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.manzanita_1 {
  width: 100px;
  height: 100px;
  position: absolute;
  top: 10%;
  left: 10%;
  animation: logo-spin infinite 1s linear;
}

.manzanita_2 {
  width: 100px;
  height: 100px;
  position: absolute;
  top: 10%;
  right: 10%;
  animation: logo-spin infinite 1s linear;
}

.manzanita_3 {
  width: 100px;
  height: 100px;
  position: absolute;
  top: 70%;
  left: 10%;
  animation: logo-spin infinite 1s linear;
}

.manzanita_4 {
  width: 100px;
  height: 100px;
  position: absolute;
  top: 70%;
  right: 10%;
  animation: logo-spin infinite 1s linear;
}

@keyframes logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}